.content {
    z-index: 2;
    position: absolute;
    top: 33%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    text-align:center;
}

.videoWrap {
    position:absolute;
    top:0;
    left:0;
    width:100vw;
    height:100vh;
    overflow:hidden;
    z-index: 0;
    background:#eee;
    opacity:0.70;
}

video {
    min-height:100%;
    min-width:100%;
}


